<template>
  <div id="share">
    <div class="content">
      <div class="imgBox">
        <div class="imgWrapper">
          <img :src="img" alt />
        </div>
      </div>
      <div class="messageBox">
        <p>
          <i class="iconfont icon-changan"></i>
          <span>长按图片发送给好友</span>
        </p>
      </div>
    </div>
    <van-overlay :show="load" :z-index="10000" class="loadBox" :lock-scroll="true">
      <van-loading class="load" type="spinner" color="#1989fa" :vertical="true" v-if="load" />
    </van-overlay>
  </div>
</template>
<script>
import { getShareImg } from "../assets/javascript/apis.js";

export default {
  data() {
    return {
      img: "",
      getImgFlag: true,
      load: false,
    };
  },
  created() {
    this.getData();
    //设置分享链接
    this.$shareConfig();
  },
  methods: {
    getData() {
      this.load = true;

      if (!this.getImgFlag) return;
      this.getImgFlag = false;

      const data = {
        ac_id: this.$store.state.distributionInfo.ac_id, //从store中拿到该参数，添加判断，如果没有，则提示失败信息，跳转个人中心页面
      };

      getShareImg(data).then((res) => {
        this.getImgFlag = true;
        this.load = false;
        if (res.status == "success") {
          this.img = res.url;
        } else {
          this.$toast({
            message: "抱歉，分享失败，请重新分享",
            position: "top",
          });
          this.$router.push("/detail");
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
#share {
  width: 100%;
  height: 100%;
  background: rgba(242, 244, 246, 1);
  display: flex;
  flex-direction: column;
  .loadBox {
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .imgBox {
      flex-grow: 1;
      width: 100%;
      position: relative;
      .imgWrapper {
        width: 325px;
        height: 520px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        img {
          width: 325px;
          height: 520px;
        }
      }
    }
    .messageBox {
      width: 100%;
      height: 40px;
      line-height: 40px;
      text-align: center;
      background: rgba(76, 170, 241, 1);
      p {
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: rgba(255, 255, 255, 1);
        span {
          padding-left: 10px;
        }
      }
    }
  }
}
</style>